
import React, { useState,useEffect,useContext, createContext, useReducer } from 'react';
import PropTypes from 'prop-types';
import { makeStyles } from '@material-ui/core/styles';
import Box from '@material-ui/core/Box';
import { Collapse,Button,Tooltip } from '@material-ui/core';
import PageTitle from '../../../components/PageTitle/PageTitle';
import IconButton from '@material-ui/core/IconButton';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Typography from '@material-ui/core/Typography';
import Paper from '@material-ui/core/Paper';
import KeyboardArrowDownIcon from '@material-ui/icons/KeyboardArrowDown';
import KeyboardArrowUpIcon from '@material-ui/icons/KeyboardArrowUp';
import DeleteForeverIcon from '@material-ui/icons/DeleteForever';
import DeleteIcon from '@material-ui/icons/Delete';
import EditIcon from '@material-ui/icons/Edit';
import { Restore } from '@material-ui/icons';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import RestoreFromTrashIcon from '@material-ui/icons/RestoreFromTrash';



const useRowStyles = makeStyles({
  root: {
    '& > *': {
      borderBottom: 'unset',
    },
  },
});

function createData(name, calories, fat, carbs, protein, price) {
  return {
    name,
    calories,
    fat,
    carbs,
    protein,
    price,
    history: [
      { date: '2020-01-05', customerId: '11091700', amount: 3 },
      { date: '2020-01-02', customerId: 'Anonymous', amount: 1 },
    ],
  };
}

export const actionContext = createContext();

const deletePost = (val) =>{
  fetch('https://test-fevison.herokuapp.com/posts/del', {
    method: 'POST',
    body:JSON.stringify(val),
    headers: {
      'Content-Type': 'application/json',
   },
   }).then(respose=>respose.json())
    .then(doc=>{
      console.log(doc)
    })
}
const restorePost = (val) =>{
  fetch('https://test-fevison.herokuapp.com/posts/tra', {
    method: 'PUT',
    body:JSON.stringify(val),
    headers: {
      'Content-Type': 'application/json',
   },
   }).then(respose=>respose.json())
    .then(doc=>{
      console.log(doc)
    })
}
const intiValues = {
    userId:null,
    view:null
  
}

const reducer = (state,action) => {
    switch(action.type){
        case "DELETE":
            return{
                ...state,
                userId: action.payload,
                view:"delete"


            }
        case "RESTORE":
          console.log(state)
          restorePost({ userId:state.userId, status:"active"})
          return {
            ...state,
            userId:action.payload
          }
        
        case "CONFIRM-DELETE":
            deletePost({userId: state.userId})
            return {
                ...state,
                usetId: action.payload,
                view:null
            }
        case "CANCEL":
          return {view: null}
    }

}

function Row(props) {
  const { row } = props;
  const [open, setOpen] = React.useState(false);
  const classes = useRowStyles();

  const { dispatch } = useContext(actionContext);

  const actionHandler = (val,ty)=> {
    dispatch({
      type:ty,
      payload:val
    })
    console.log(val)
 
  }

  return (
    <React.Fragment>
      <TableRow className={classes.root}>
        

        <TableCell component="th" scope="row">
        <Tooltip title="DELETE" arrow>
          <Button onClick={()=>actionHandler(row._id,'DELETE')}>
             <DeleteForeverIcon />
          </Button>
        </Tooltip>
          
          <Tooltip title="RESTORE" arrow>
           <Button onClick={()=>{actionHandler(row._id,'RESTORE')}}>
           <RestoreFromTrashIcon />           
           </Button>
          </Tooltip>
        </TableCell>
        <TableCell>
           
          <IconButton aria-label="expand row" size="small" onClick={() => setOpen(!open)}>
            {open ? <KeyboardArrowUpIcon /> : <KeyboardArrowDownIcon />}
          </IconButton>
        </TableCell>
        <TableCell component="th" scope="row">
          {row.title}
        </TableCell>
        <TableCell component="th" scope="row">
          {row.category}
        </TableCell>
       
      </TableRow>
      <TableRow>
        <TableCell style={{ paddingBottom: 0, paddingTop: 0 }} colSpan={6}>
          <Collapse in={open} timeout="auto" unmountOnExit>
            <Box margin={1}>
              <Typography variant="h6" gutterBottom component="div">
                Paragraph
              </Typography>
              <Table size="small" aria-label="purchases">
                <TableHead>
                  <TableRow>
                    <TableCell>Date</TableCell>
                    <TableCell>{row.created}</TableCell>
                    
                  </TableRow>
                </TableHead>
                <TableBody>
                    <TableRow >
                      <TableCell component="th" scope="row">
                        {row.paragraph}
                      </TableCell>

                    </TableRow>
                </TableBody>
              </Table>
            </Box>
          </Collapse>
        </TableCell>
      </TableRow>
    </React.Fragment>
  );

}

Row.propTypes = {
  row: PropTypes.shape({
    calories: PropTypes.number.isRequired,
    carbs: PropTypes.number.isRequired,
    fat: PropTypes.number.isRequired,
    history: PropTypes.arrayOf(
      PropTypes.shape({
        amount: PropTypes.number.isRequired,
        customerId: PropTypes.string.isRequired,
        date: PropTypes.string.isRequired,
      }),
    ).isRequired,
    name: PropTypes.string.isRequired,
    price: PropTypes.number.isRequired,
    protein: PropTypes.number.isRequired,
  }).isRequired,
};

const rows = [
  createData('Frozen yoghurt', 159, 6.0, 24, 4.0, 3.99),
  createData('Ice cream sandwich', 237, 9.0, 37, 4.3, 4.99),
  createData('Eclair', 262, 16.0, 24, 6.0, 3.79),
  createData('Cupcake', 305, 3.7, 67, 4.3, 2.5),
  createData('Gingerbread', 356, 16.0, 49, 3.9, 1.5),
];

export default function TrashList() {
    const [trashList, setTrashList] = useState([]);
    const [state, dispatch] = useReducer(reducer, intiValues);


    useEffect(() => {
        fetch('https://test-fevison.herokuapp.com/posts/gettra', {
          headers: {
            'Content-Type': 'application/json',
         },
        method: 'GET',
  
          }).then(respose=>respose.json())
          .then(doc=>{
            console.log(doc)
            doc.map((ele)=>setTrashList((pre)=>[...pre, ele]))
          }
          )
          .catch(err=>console.log(err))
          return ( () => setTrashList(()=>[]))
        
       
      }, [state])


      const customDiloge = (
        <Dialog open={true}  aria-labelledby="form-dialog-title">
         <DialogContent>
            <DialogTitle id="form-dialog-title">Delete Confirmation</DialogTitle>
            <DialogContentText id="alert-dialog-description">Do You Realy Want To Delete This Blog</DialogContentText>
         </DialogContent>
      
         <DialogActions>
            <Button  color="primary" onClick={()=>dispatch({type: 'CANCEL'})}>
                  CANCEL
            </Button>
            <Button onClick={()=>dispatch({type: 'CONFIRM-DELETE'})} color="primary" autoFocus>
                  Agree
            </Button>
         </DialogActions>
      
      </Dialog>
      )

const dialogView = () => {
  switch(state.view){
    case 'delete':
      return customDiloge
    default:
      return

  }
}



  return (
      <actionContext.Provider value={{ state, dispatch }}>
      <PageTitle title='Trash List' />
        
        {dialogView()}
    <TableContainer component={Paper}>
      <Table aria-label="collapsible table">
        <TableHead>
          <TableRow>
            <TableCell >Action</TableCell>
            <TableCell >View</TableCell>
            <TableCell>Title</TableCell>
            <TableCell>Categories</TableCell>

            <TableCell align="right"></TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {trashList.map((row) => (
            <Row key={row._id} row={row} />
          ))}
        </TableBody>
      </Table>
    </TableContainer>
    </actionContext.Provider>
  );
}
