import React from 'react'
import { Container, Navbar, Nav, DropdownButton, Dropdown, OverlayTrigger, Tooltip, Button } from 'react-bootstrap';
import Rbutton from '../Rbutton/Rbutton';
import './Navbar.css'
import { Link } from 'react-router-dom';

const NavbarE = () => {
  return (
    <Navbar bg="dark" fixed="top" variant="primary">
    <Container>
      <Navbar.Brand href="#home"><Link to='/'><img className="navbar-brand" src='../img/logo.png' /></Link></Navbar.Brand>
      <Nav className="me-auto nav-cus ml-auto" activeKey="/home">

        <Nav.Item className="nav-item">
          <Nav.Link variant="secondary" className="nav-item"><Link to='/'>home</Link></Nav.Link>
        </Nav.Item>
        <Nav.Item >
          <Nav.Link eventKey="link-1" className="nav-item"><Link to='/feature'>Featurs</Link></Nav.Link>
        </Nav.Item>

        <Nav.Item >
          <Nav.Link eventKey="price" className="nav-item"><Link to='/Price'>Price</Link></Nav.Link>
        </Nav.Item>

        <Nav.Item >
          <Nav.Link eventKey="link-2" className="nav-item">
            <Dropdown className="d-inline mx-2">
              <Dropdown.Toggle as={Link} containt='' id="dropdown-autoclose-true">
                Pages
              </Dropdown.Toggle>

              <Dropdown.Menu>
              <Link to='/feature'>
                <Dropdown.Item href="/feature">Featurs</Dropdown.Item>
              </Link>
              <Link to='/price'>
                  <Dropdown.Item href="price">Price</Dropdown.Item>
              </Link>
              <Link to='/element'>
                <Dropdown.Item href="elament">Element</Dropdown.Item>
              </Link>
              </Dropdown.Menu>
            </Dropdown>
          </Nav.Link>
        </Nav.Item>

        <Nav.Item >
          <Nav.Link eventKey="link-1" className="nav-item"> <Link to='/blogs/'>
                Blogs
              </Link>
              </Nav.Link>
              
            {/* <OverlayTrigger
              placement={'bottom'}
              overlay={
                <Dropdown.Menu show>
                  <Dropdown.Item href="#">Feature</Dropdown.Item>
                  <Dropdown.Item href="#">Element</Dropdown.Item>
                  <Dropdown.Item href="#">Menu Item</Dropdown.Item>
                </Dropdown.Menu>
              }
            >
              <Link to='/pages' >Pages</Link>
            </OverlayTrigger> */}
        </Nav.Item>

        <Nav.Item >
          <Nav.Link eventKey="link-2" className="nav-item"><Link to='/contacts'>Contact</Link></Nav.Link>
        </Nav.Item>

      </Nav>
      <Nav>
        {/* <Rbutton /> */}
      </Nav>

    </Container>
  </Navbar>
  )
}

export default NavbarE
