export const userContext = React.createContext();

const initialState = {
  userId: null,
  view: null,
};

const deletePost = (val) => {
  fetch('https://test-fevison.herokuapp.com/posts/del', {
    method: 'POST',
    body: JSON.stringify(val),
    headers: {
      'Content-Type': 'application/json',
    },
  }).then(respose => respose.json())
    .then(doc => {
      console.log(doc)
    })
}
const trashPost = (val) => {
  fetch('https://test-fevison.herokuapp.com/posts/tra', {
    method: 'PUT',
    body: JSON.stringify(val),
    headers: {
      'Content-Type': 'application/json',
    },
  }).then(respose => respose.json())
    .then(doc => {
      console.log(doc)
    })
}


const reducer = (state, action) => {
  switch (action.type) {
    case "DELETE":
      console.log({ state })
      return {
        ...state,
        userId: action.payload,
        view: 'delete'
      };
    case "CONFIRM-DELETE":
      deletePost({ userId: state.userId })
      return {
        ...state,
        view: null
      }


    case "EDIT":
      console.log(state)


      if (state.view == 'edit') {
        return {
          ...state,
          userId: action.payload,
          view: 'editRefresh'
        }
      } else if (state.view == 'editRefresh') {
        return {
          ...state,
          view: 'edit'
        }

      }
      else {
        return {
          ...state,
          userId: action.payload,
          view: 'edit'
        }
      }


    case "TRASH":
      console.log(state)
      trashPost({ userId: state.userId, status: 'deActive' })
      return {
        ...state,
        userId: action.payload,
        view: 'trash'
      };

    case "CANCEL":
      console.log(state)
      return {
        ...state,
        view: null
      }
    default:
      return state;
  }
};


function Row(props) {
  const { row } = props;
  const [open, setOpen] = React.useState(false);
  const classes = useRowStyles();
  const { dispatch } = React.useContext(userContext);
  const [selected, setSelected] = React.useState([]);

  const handleClickRow = (event, name) => {
    const selectedIndex = selected.indexOf(name);
    let newSelected = [];


    if (selectedIndex === -1) {
      newSelected = newSelected.concat(selected, name);
    } else if (selectedIndex === 0) {
      newSelected = newSelected.concat(selected.slice(1));
    } else if (selectedIndex === selected.length - 1) {
      newSelected = newSelected.concat(selected.slice(0, -1));
    } else if (selectedIndex > 0) {
      newSelected = newSelected.concat(
        selected.slice(0, selectedIndex),
        selected.slice(selectedIndex + 1),
      );
    }

    setSelected(newSelected);
    console.log(newSelected)
  };



  const actionHandler = (val, ty) => {
    dispatch({
      type: ty,
      payload: val
    })
    console.log(val)

  }
  const isSelected = (name) => {  
    return selected.indexOf(name) !== -1;
  }

  const isItemSelected = props.isSelected(row._id);
  

  return (
    <React.Fragment>
      <TableRow className={classes.root}
      hover
      onClick={(event) => props.handleClickRow(event, row._id)}
      role="checkbox"
      aria-checked={isItemSelected}
      selected={isItemSelected}

      >
        <TableCell padding="checkbox">
          <Checkbox
            checked={isItemSelected}
            inputProps={`enhanced-table-checkbox-${row._id}`}
          />
        </TableCell>


        <TableCell component="th" scope="row">
        <Tooltip title="Delete" arrow>
          <Button onClick={() => actionHandler(row._id, 'DELETE')}>
            <DeleteForeverIcon />
          </Button>
          </Tooltip>
          <Tooltip title="Edit" arrow>
          <Button onClick={() => actionHandler(row, 'EDIT')}>
            <EditIcon />
          </Button>
          </Tooltip>
        <Tooltip title="Trash" arrow>
          <Button onClick={() => { actionHandler(row._id, 'TRASH') }}>
            <DeleteIcon />
          </Button>
        </Tooltip>  
        </TableCell>
        <TableCell>

          <IconButton aria-label="expand row" size="small" onClick={() => setOpen(!open)}>
            {open ? <KeyboardArrowUpIcon /> : <KeyboardArrowDownIcon />}
          </IconButton>
        </TableCell>
        <TableCell component="th" scope="row">
          {row.title}
        </TableCell>
        <TableCell component="th" scope="row">
          {row.category}
        </TableCell>

      </TableRow>
      <TableRow>
        <TableCell style={{ paddingBottom: 0, paddingTop: 0 }} colSpan={6}>
          <Collapse in={open} timeout="auto" unmountOnExit>
            <Box margin={1}>
              <Typography variant="h6" gutterBottom component="div">
                Paragraph
              </Typography>
              <Table size="small" aria-label="purchases">
                <TableHead>
                  <TableRow>
                    <TableCell>Date</TableCell>
                    <TableCell>{row.created}</TableCell>

                  </TableRow>
                </TableHead>
                <TableBody>
                  <TableRow >
                    <TableCell component="th" scope="row">
                      {row.paragraph}
                    </TableCell>

                  </TableRow>
                </TableBody>
              </Table>
            </Box>
          </Collapse>
        </TableCell>
      </TableRow>
    </React.Fragment>
  );
}





const rows = [
  createData('Frozen', 159, 6.0, 24, 4.0, 3.99),
  createData('Ice cream sandwich', 237, 9.0, 37, 4.3, 4.99),
  createData('Eclair', 262, 16.0, 24, 6.0, 3.79),
  createData('Cupcake', 305, 3.7, 67, 4.3, 2.5),
  createData('Gingerbread', 356, 16.0, 49, 3.9, 1.5),
];




export default function CollapsibleTable() {
  const [postList, setPostList] = useState([])
  const [state, dispatch] = React.useReducer(reducer, initialState);
  const [selected, setSelected] = useState([]);
  const [snackbarToggle, setSnackbarToggle] = useState(false)
  const [deleteAllToggal, setDeleteAllToggal] =useState(false);
  const [userData, setUserData] = useState(state.userId)
  const [paginationData, setPaginationData] = useState({
    pageNo: 0, rowPage: 10, total: 100
  })
  let { pageNo, rowPage } = paginationData;


  useEffect(() => {
    fetch(`https://test-fevison.herokuapp.com/posts/filter?page=${paginationData.pageNo}&row=${paginationData.rowPage}`, {
      headers: {
        'Content-Type': 'application/json',
      },
      method: 'GET',

    }).then(respose => respose.json())
      .then(doc => {
        if (doc == "") {
          return
        }
        (doc.getPost).map((ele) => setPostList((pre) => [...pre, ele]))
        setPaginationData((pre) => {
          return { ...pre, total: doc.total }
        })
        console.log(postList);
      })
      .catch(err => console.log(err))
    return (() => setPostList(() => []))
  }, [state, pageNo, rowPage])

  const deleteAllURL = (val)=>{
    fetch('https://test-fevison.herokuapp.com/posts/delall', {
    method: 'POST',
    body: JSON.stringify({id:val}),
    headers: {
      'Content-Type': 'application/json',
    }
    
  }).then(respose => respose.json())
    .then(doc => {
      console.log(doc)
  })

  }


  const customDiloge = (
    <Dialog open={true} aria-labelledby="form-dialog-title">
      <DialogContent>
        <DialogTitle id="form-dialog-title">Delete Confirmation</DialogTitle>
        <DialogContentText id="alert-dialog-description">Do You Realy Want To Delete This Blog</DialogContentText>
      </DialogContent>

      <DialogActions>
        <Button color="primary" onClick={
          () =>{ 
            dispatch({ type: 'CANCEL' })
            setDeleteAllToggal(!deleteAllToggal)
          }
          }>
          CANCEL
        </Button>
        {deleteAllToggal ? <Button onClick={()=>deleteAllConfirm()}  color="primary" children={'Delete All'} autoFocus /> :
        <Button onClick={() => dispatch({ type: 'CONFIRM-DELETE' })}  color="primary" autoFocus>
          Agree
        </Button>}
      </DialogActions>

    </Dialog>
  )

  const dialogView = () => {
    switch (state.view) {
      case 'delete':
        return customDiloge
      case 'edit':
        return <PostsPage data={state.userId} />
      case 'trash':
        return console.log('trahd')
      case 'editRefresh':
        return

      default:
        return

    }
  }
  const Alert = (props) => {
    return <MuiAlert elevation={6} variant="filled" {...props} />;
  }
  const handleChangePage = (event, newPage) => {
    // setPage(newPage);
    setPaginationData((pre) => {
      return {
        ...pre, pageNo: newPage
      }
    })

    console.log(paginationData)
  }
  const useToolbarStyles = makeStyles((theme) => ({
    root: {
      paddingLeft: theme.spacing(2),
      paddingRight: theme.spacing(1),
    },
    highlight:
      theme.palette.type === 'light'
        ? {
            color: theme.palette.secondary.main,
            backgroundColor: (theme.palette.secondary.light,0.70)
          
          }
        : {
            color: theme.palette.text.primary,
            backgroundColor: theme.palette.secondary.dark,
          },
    title: {
      flex: '1 1 100%',
    },
  }));
  const EnhancedTableToolbar = (props) => {
    const classes = useToolbarStyles();
    const { numSelected } = props;
  
    return (
      <Toolbar
      className={(classes.root, {
        [classes.highlight]: numSelected > 0,
      })}
       
      >
        {numSelected > 0 ? (
          <Typography className={classes.title} color="inherit" variant="subtitle1" component="div">
            {numSelected} selected
          </Typography>
        ) : (
          <Typography className={classes.title} variant="h6" id="tableTitle" component="div">
            BlogList
          </Typography>
        )}
  
        {numSelected > 0 ? (
          <Tooltip title="Delete">
            <IconButton aria-label="delete" onClick={props.deleteAllHandler}>
              <DeleteIcon />
            </IconButton>
          </Tooltip>
        ) : (
          <Tooltip title="Filter list">
            <IconButton aria-label="filter list" >
              <FilterListIcon />
            </IconButton>
          </Tooltip>
        )}
      </Toolbar>
    );
  };


  const handleChangeRowsPerPage = (event) => {
    // setRowsPerPage(parseInt(event.target.value, 10));
    // setPage(0);

    setPaginationData((pre) => {
      return { ...pre, pageNo: 0, rowPage: parseInt(event.target.value) }
    })

  };
  const handleClickRow = (event, name) => {
    const selectedIndex = selected.indexOf(name);
    let newSelected = [];


    if (selectedIndex === -1) {
      newSelected = newSelected.concat(selected, name);
    } else if (selectedIndex === 0) {
      newSelected = newSelected.concat(selected.slice(1));
    } else if (selectedIndex === selected.length - 1) {
      newSelected = newSelected.concat(selected.slice(0, -1));
    } else if (selectedIndex > 0) {
      newSelected = newSelected.concat(
        selected.slice(0, selectedIndex),
        selected.slice(selectedIndex + 1),
      );
    }

    setSelected(newSelected);
    console.log(newSelected)
  };
  const handleSelectAllClick = (event) => {
    if (event.target.checked) {
      const newSelecteds = postList.map((n) => n._id);
      setSelected(newSelecteds);
      console.log(selected)
      return;
    }
    setSelected([]);
  };
  const isSelected = (name) => selected.indexOf(name) !== -1;


  const deleteAllHandler = ()=> {
    dispatch({ type: 'DELETE' })
    setDeleteAllToggal(!deleteAllToggal)
  }
  const deleteAllConfirm =()=>{
    dispatch({type : 'CANCEL'})
    setDeleteAllToggal(!deleteAllToggal)
    deleteAllURL(selected)
    setSelected(()=>[])
  }
  

  return (
    <userContext.Provider value={{
      state,
      dispatch
    }}>
      <PageTitle title='Blog List' />
      {/* <Snackbar open={snackbarToggle} anchorOrigin={{ vertical:"top", horizontal:"right" }} children={<Alert severity="success">This is a success message!</Alert>} /> */}

      {dialogView()}

      <EnhancedTableToolbar numSelected={selected.length} deleteAllHandler={deleteAllHandler} />
       
      <TableContainer component={Paper}>
        <Table aria-label="collapsible table">
          <TableHead>
            <TableRow>
              <TableCell>
                <Checkbox
                indeterminate={selected.length > 0 && selected.length < paginationData.rowPage}
                checked={paginationData.rowPage > 0 && selected.length === paginationData.rowPage}
                onChange={handleSelectAllClick}
                inputProps={{ 'aria-label': 'select all desserts' }}
                />
              </TableCell>
              <TableCell >Action</TableCell>
              <TableCell >View</TableCell>
              <TableCell>Title</TableCell>
              <TableCell>Category</TableCell>

              <TableCell align="right"></TableCell>

            </TableRow>
          </TableHead>
          {/* <EnhancedTableHead
              classes={classes}
              numSelected={selected.length}
              order={order}
              orderBy={orderBy}
              onSelectAllClick={handleSelectAllClick}
              onRequestSort={handleRequestSort}
              rowCount={rows.length}
            /> */}
          <TableBody>
            {postList.map((row) => (
              
              <Row key={row._id} row={row} isSelected={isSelected} handleClickRow={handleClickRow} />
            ))}
          </TableBody>

        </Table>
        <TablePagination
          rowsPerPageOptions={[10, 15, 25, 50, 100, 200, 500]}
          component="div"
          count={paginationData.total}
          rowsPerPage={paginationData.rowPage}
          page={paginationData.pageNo}
          onPageChange={handleChangePage}
          onRowsPerPageChange={handleChangeRowsPerPage}
        />
      </TableContainer>
    </userContext.Provider>
  );
}

