import React ,{ useEffect, useState } from 'react'
import { Grid, Icon, Tooltip} from "@material-ui/core";
import { makeStyles } from "@material-ui/styles";
import MUIDataTable from "mui-datatables";
import ComposedTextField from './form/Form';
import { Button } from '@material-ui/core';
import 'react-toastify/dist/ReactToastify.css';
// import { DeleteIcon }  from "@material-ui/icons";
import DeleteIcon from '@material-ui/icons/Delete';
import EditIcon from '@material-ui/icons/Edit';
import VisibilityIcon from '@material-ui/icons/Visibility';
import DeleteForeverIcon from '@material-ui/icons/DeleteForever';


// components
import PageTitle from "../../components/PageTitle";
import Widget from "../../components/Widget";
import Table from "../dashboard/components/Table/Table";
import mock from "../dashboard/mock";
import { DragIndicatorRounded, Message } from '@material-ui/icons';
import SimpleModal from './modal/SimpleModal';
import DraggableDialog from './deleteConfirm/DraggableDialog';
import Editform from './editForm/Editform';








const BlogForm = ( ...props ) => {
  // const [state, dispatch] = React.useReducer(reducer, initialState);
  const [tableData, setTableData] = useState([])
  const [sentDataToggler, setSentDataToggler] = useState(false);
  const [showModal, setShowModal] = useState(false);
  const [modalData, setModalData] = useState();
  const [modalType, setModalType] = useState();
  const [showDelete,setShowDelete] = useState(false)
  const [userId, setUserId] = useState('')
  const [showEdit, setShowEdit] = useState(false);

  const forDataLoop = data => data.forEach(ele=> setTableData(pre => [ ...pre, Object.values(ele) ]))
  


  
  
   
        
          useEffect(() => {

            
            
            const fetchData = () => {
            fetch('https://test-fevison.herokuapp.com/contact/')
            .then(response => response.json())
            .then(data => {
                forDataLoop(data)
                console.log(data)
              })
            }
            fetchData();
            return (()=>setTableData(()=>[]))
          },[sentDataToggler])
          
          
          
        
        
    
        
        


  

    
      // getUserData.forEach(element => {
      //   datatableData.push(Object.values(element)) 
      // });  
      const onTrashHandaler =() =>{
        fetch('https://test-fevison.herokuapp.com/contact/movetrash', {
                method: 'POST', // or 'PUT'
                headers: {
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify({_id:userId, status:'deActive'}),
                })
                .then(response => response.json(props.modalData))
                .then(data => {
                    console.log('Success:', 'tresed');
                })
                .catch((error) => {
                    console.error('Error:', error);
                });
                setSentDataToggler(()=>!sentDataToggler)
      }
      const onDeleteHandaler =() =>{

        fetch('https://test-fevison.herokuapp.com/contact/delete', {
                method: 'POST', // or 'PUT'
                headers: {
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify({_id:userId}),
                })
                .then(response => response.json(props.modalData))
                .then(data => {
                    console.log('Success:', 'dlete');
                })
                .catch((error) => {
                    console.error('Error:', error);
                });
                console.log('requested to delete');
                setSentDataToggler(()=>!sentDataToggler)
      }
        const useStyles = makeStyles(theme => ({
          tableOverflow: {
            overflow: 'auto'
          }
        }))
        const tableRowView = (value,type) => {
          console.log(`click ${value}`)
          const result =tableData.find(( [_id] ) => _id == value)
          setUserId(()=> result[0])
          if(type == 'delete'){
          setShowDelete(()=>true)
          }else if (type == 'trash') {
          onTrashHandaler()}
          // }else if (type == 'edit'){
          //   setShowEdit(()=>false)
          //   setModalData(()=>result)
          //   setShowEdit(()=>true)

          //   console.log(modalData)
            

          // }
          else{
          setModalType(()=>type)
          setModalData(()=>result)
          setShowModal(()=>true)



          }


        }
          const classes = useStyles();
          const columnsForTable = [
            {
              name: "Actions",
              options: {
                customBodyRender: (value, tableMeta, updateValue) => {

                  return (
                    <>
                    <Tooltip title="View" arrow>
                    <Button onClick={()=>{tableRowView(value,'view')}}>
                      <VisibilityIcon />
                    </Button>
                </Tooltip>

                    <Tooltip title="Edit" arrow>

                    <Button onClick={ ()=>tableRowView(value,'edit')}>
                    <EditIcon />
                  </Button>
                </Tooltip>

                  <Tooltip title="Move to Trash" arrow>
                  <Button onClick={()=>tableRowView(value,'trash')}>
                  <DeleteIcon />
                </Button>
                </Tooltip>
                <Tooltip title="Delete Parmanet" arrow>
                  <Button onClick={()=>tableRowView(value,'delete')}>
                  <DeleteForeverIcon />

                </Button>
                </Tooltip>
                </>
                  );
                }
              }
            },
            "Name",
            "Messege", 
            "Email",
            "Subject"
          ];
         const sentDataToggalHandler = () =>{
          setSentDataToggler((toggal)=>!toggal)
         }
          
          return (
            <>
              
               {showEdit && <Editform formData={modalData}  />}
              {showDelete && <DraggableDialog setOpen={true} controlAction={onDeleteHandaler} setOpenDilog={()=>setShowDelete(()=>false)} message={'Are You Sure Want To Delete ?'} /> }
               
              {showModal && <SimpleModal sentDataToggal={sentDataToggalHandler} taggalModal={()=>setShowModal(()=>false)} modalData={modalData} modalType={modalType} message={'Are You Sure Want To Delete ?'} />}
            
              <PageTitle title="BlogForm" />
              <Grid container spacing={4}>
                 
                <Grid item xs={12}>
                  <Grid item xs={8}>
                    <ComposedTextField sentDataToggal={sentDataToggalHandler} formData={[]} reqType={'view'} />
                    {/* { console.log(getUserData)} */}
                    {
                    //  (Object.values())
                    }
                     {/* ))} */}
                    {/* {convertedData} */}
                    </Grid>

                </Grid>
                <Grid item xs={12}>
          <MUIDataTable
            title="Blog List"
            data={tableData}
            columns={columnsForTable}
            onSelectionChange={(v)=>console.log(v)}
            // onRowClick={(valu)=>console.log(valu)}
            
            options={{
              filterType: "checkbox",
              textLabels: {
                body: {
                  noMatch: " Data Loading...",
                  toolTip: "Sort",
                  columnHeaderTooltip: column => `Sort for ${column.label}`
                },
            }}}
          />
        </Grid>

                
              </Grid>
              {/* </BlogContext.Provider> */}
              
            </>
          );
        }
        
    


export default BlogForm
