import React, { useState, useEffect } from 'react'
import PageTitle from '../../components/PageTitle/PageTitle';
import { makeStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import Box from '@material-ui/core/Box';
import { Select, Snackbar } from '@material-ui/core';
import MenuItem from '@material-ui/core/MenuItem';
import Chip from '@material-ui/core/Chip';
import { useTheme } from '@material-ui/core/styles';
import { DropzoneArea } from 'material-ui-dropzone';
import MuiAlert from '@material-ui/lab/Alert';

import { FormControl, TextField, OutlinedInput, Input, InputLabel, FormHelperText, Grid } from '@material-ui/core';


const useStyles = makeStyles((theme) => ({
  formControl: {
    margin: theme.spacing(1),
    minWidth: 300,
    maxWidth: 600,
    borderRadius: 4,
  },
  chips: {
    display: 'flex',
    flexWrap: 'wrap',
  },
  chip: {
    margin: 2,
  },
  noLabel: {
    marginTop: theme.spacing(3),
  },
}));

const ITEM_HEIGHT = 48;
const ITEM_PADDING_TOP = 8;
const MenuProps = {
  PaperProps: {
    style: {
      maxHeight: ITEM_HEIGHT * 4.5 + ITEM_PADDING_TOP,
      width: 250,
    },
  },
};

const names = [
  'Oliver Hansen',
  'Van Henry',
  'April Tucker',
  'Ralph Hubbard',
  'Omar Alexander',
  'Carlos Abbott',
  'Miriam Wagner',
  'Bradley Wilkerson',
  'Virginia Andrews',
  'Kelly Snyder',
];

function getStyles(name, personName, theme) {
  return {
    fontWeight:
      personName.indexOf(name) === -1
        ? theme.typography.fontWeightRegular
        : theme.typography.fontWeightMedium,
  };
}


const PostsPage = ({ ...props }) => {
  const [title, setTitle] = useState(props.data.title);
  const [paragraph, setParagraph] = useState(props.data.paragraph);
  const [categoryList, setCategoryList] = useState([]);
  const [category, setCategory] = useState(props.data.category || [])
  const [image, setImage] = useState([])
  const [fileObjects, setFileObjects] = useState([]);
  const [snackbarToggle, setSnackbarToggle] = useState(false);
  const [snackData, setSnackData] = useState({});
  const [seoKey, setSeoKey] = useState(props.data.seo_keyword);
  const [seoTitle, setSeoTitle] = useState(props.data.seo_title);
  const [seoDes, setSeoDes] = useState(props.data.seo_description);
  const [seoData, setSeoData] = useState({})
  const [buttonToggle, setButtonTogle] = useState(false);


  const insertForm = (sendData) => {
    fetch('https://test-fevison.herokuapp.com/posts/add', {
      method: 'POST',
      body: sendData
    })
      .then(respose => respose.json())
      .then(doc => {
        console.log(doc)
        SneckControl('success', doc.message)
        console.log('requestad for add')
        for (var value of sendData.values()) {
          console.log(value);
        }
      })
      .catch(err => console.log(err))
  }
  const updateForm = (sendData) => {
    fetch('https://test-fevison.herokuapp.com/posts/update', {
      method: 'PUT',
      body: sendData,
    })
      .then(respose => respose.json())
      .then(doc => {
        console.log(doc)
        SneckControl('success', doc.message)
        console.log('for update request')

      })
      .catch(err => console.log(err))
  }


  useEffect(() => {
    fetch('https://test-fevison.herokuapp.com/category/', {
      headers: {
        'Content-Type': 'application/json',
      },
      method: 'GET',

    }).then(respose => respose.json())
      .then(doc => {
        console.log(doc)
        doc.map((ele) => setCategoryList((pre) => [...pre, ele]))
      }
      )
      .catch(err => console.log(err))
    return (() => setCategoryList(() => []))
  }, [])
  console.log(categoryList)


  const classes = useStyles();
  const theme = useTheme();



  const handleChangeSelect = (event) => {
    setCategory(event.target.value);


  }

  // const handleChangeMultiple = (event) => {
  //   const { options } = event.target;
  //   const value = [];
  //   for (let i = 0, l = options.length; i < l; i += 1) {
  //     if (options[i].selected) {
  //       value.push(options[i].value);
  //     }
  //   }
  //   setPersonName(value);
  // };  
  const onblurhandler = (a, b) => {



    console.log('onblur is ready')


  }
  const handleChangeTitle = (event) => {
    setTitle(() => event.target.value)
  }
  const handleChangeParagraph = (event) => {
    setParagraph(() => event.target.value)

  }
  const submitHandler = (event) => {
    event.preventDefault();
    if (title == '' || paragraph == "" || category == "") {
      return console.log('fileds cant be empty')
    }
    const sendData = new FormData()
    sendData.append('title', title);
    sendData.append('seo_title',seoTitle)
    sendData.append('seo_keyword',seoKey)
    sendData.append('seo_description',seoDes)
    sendData.append('paragraph', paragraph)
    category.forEach(element => {
      sendData.append('category', element)
    });
    image.forEach(element => {
      sendData.append('image', element)
    });
    if (props.data._id) {
      sendData.append('userId', props.data._id)
      updateForm(sendData)
    } else {
      insertForm(sendData);
    }

    setTitle(() => '')
    setParagraph(() => '')
    setCategory(() => [])
    setSeoTitle('')
    setSeoDes('')
    setSeoKey('')







  }
  console.log(image)
  console.log(title)
  console.log(props.data._id);


  const Alert = (props) => {
    return <MuiAlert elevation={6} variant="filled" {...props} />;
  }
  const SneckControl = (type, message) => {
    setSnackData(() => (
      {
        type: type,
        message: message
      }
    ))
    setSnackbarToggle(() => true)
    setTimeout(() => {
      setSnackbarToggle(() => false)
    }, 3000);
  }
  const seoChangeHandler = (val,nam)=>{
    
    setSeoData((pre)=>{
      return{
        ...pre,
       [nam]:val
      }
    
    })
    console.log(seoData)
  }
 const seoChangeHandlerKey =  (e)=>{
   setSeoKey(e.target.value)
 }
 const seoChangeHandlerTit =  (e)=>{
  setSeoTitle(e.target.value)
}
const seoChangeHandlerDes =  (e)=>{
  setSeoDes(e.target.value)
}
console.log(seoTitle,seoDes,seoKey)
  return (
    <>
      <Snackbar open={snackbarToggle} anchorOrigin={{ vertical: "top", horizontal: "right" }} children={<Alert severity="success">{snackData.message}</Alert>} />

      <form className={classes.root} noValidate autoComplete="off" onSubmit={submitHandler} >
        <div>
        <Box py={5} width="5">
          <FormControl variant="outlined" fullWidth>
               <TextField id="outlined-basic" label="SEO Title" onChange={seoChangeHandlerTit} size='small' value={seoTitle} variant="outlined" name='title' />
               <TextField id="outlined-basic" label="SEO Keyword" size='small' onChange={seoChangeHandlerKey} value={seoKey} variant="outlined" name='keyword' />
               <TextField id="outlined-basic" label="SEO Description" size='small' onChange={seoChangeHandlerDes} value={seoDes} variant="outlined" name='description'/>
          </FormControl>
        </Box>
          <FormControl variant="outlined" fullWidth>
          <InputLabel htmlFor="component-outlined" >Title</InputLabel>
          <OutlinedInput required id="component-outlineds" onChange={(e) => setTitle(e.target.value)} onBlur={(e) => onblurhandler(e.target.name,e.target.value)} name="title" value={title} label="Title" aria-describedby="my-helper-text" />
          <FormHelperText color="primary" id="my-helper-text" >We'll never share your email.</FormHelperText>

          </FormControl>
          <Box py={1} width="1">
        {/* <FormControl> */}
          <FormControl variant="outlined" fullWidth>
          <InputLabel htmlFor="component-outlined" >Paragraph</InputLabel>
          <OutlinedInput
          name="description"
          value={paragraph}
          label="paragraph"
          rows={10}
          variant="outlined"
          onChange={(e) => setParagraph(e.target.value)}
          multiline
          fullWidth
          aria-describedby="my-helper-text"
          />
          <FormHelperText color="primary" id="my-helper-text" >Write Your Paragraph Here</FormHelperText>
          </FormControl>

          </Box>


        {/* <MUIRichTextEditor
                  label="Start Typing..."
                   name="paragraph" 
                   value={paragraph}
                   onChange={value => {
                    const content = JSON.stringify(
                      convertToRaw(value.getCurrentContent())
                    );
                    const textValuExtrected = JSON.parse(content).blocks[0].text
                    setParagraph(textValuExtrected)
                    console.log(()=>paragraph)
                  }}
                   onSave={saveHandler} /> */}
          <Box mt={2}>

          <FormControl variant="outlined" className={classes.formControl}>
          <InputLabel id="demo-mutiple-chip-label">Select Categories</InputLabel>
          <Select
          variant="outlined"
          labelId="Select Categories"
          id="demo-mutiple-chip"
          multiple
          name="category"
          value={category}

          onChange={handleChangeSelect}
          input={<Input id="select-multiple-chip" />}
          renderValue={(selected) => (
          <div className={classes.chips}>
        {selected.map((value,i) => (
          <Chip key={i} label={value} className={classes.chip} />
        ))}
          </div>
        )}
          MenuProps={MenuProps}
          >
        {categoryList.map((name) => (
          <MenuItem key={name._id} value={name.categoryTitle} style={getStyles(name, category, theme)}>
        {name.categoryTitle}
          </MenuItem>
        ))}
          </Select>
          </FormControl>
          <formControl>
          <DropzoneArea filesLimit={999}  onAdd={newFileObjs => {
          console.log('onAdd', newFileObjs);
          setFileObjects([].concat(fileObjects, newFileObjs));
        }} onChange={(files) => setImage((pre) => (files)) } clearOnUnmoun={true}  filesLimit={3} />
          </formControl>
          </Box>
          <Box mt={5}>
        { props.data._id?<Button variant="contained" color="primary" mt={5} type ='submit' >Update</Button>:
          <Button type ="submit" variant="contained" color="primary" mt={5} >Add Post</Button>}
          </Box>
        </div>
      </form>



    </>
  );



}

export default PostsPage
