import React from "react";
import {
  Route,
  Switch,
  Redirect,
  withRouter,
} from "react-router-dom";
import classnames from "classnames";
import {Box, IconButton, Link} from '@material-ui/core'
import Icon from '@mdi/react'

//icons
import {
  mdiFacebook as FacebookIcon,
  mdiTwitter as TwitterIcon,
  mdiGithub as GithubIcon,
} from '@mdi/js'

// styles
import useStyles from "./styles";

// components
import Header from "../Header";
import Sidebar from "../Sidebar";

// pages
import Dashboard from "../../pages/dashboard";
import Typography from "../../pages/typography";
import Notifications from "../../pages/notifications";
import Maps from "../../pages/maps";
import Tables from "../../pages/tables";
import BlogForm from "../../pages/blogform/BlogForm";
import Icons from "../../pages/icons";
import Charts from "../../pages/charts";
import trashPage from '../../pages/trashPage/TrashPage'
import PostsPage from "../../pages/posts/PostsPage";
import Categories from "../../pages/posts/categories/Categories";
import ViewPostList from "../../pages/posts/viewPost/ViewPostList";
import TrashList from "../../pages/posts/trashList/TrashList";
import AddPost from "../../pages/posts/addPost/AddPost";

// context
import { useLayoutState } from "../../context/LayoutContext";

function Layout(props) {
  var classes = useStyles();

  // global
  var layoutState = useLayoutState();

  return (
    <div className={classes.root}>
        <>
          <Header history={props.history} />
          <Sidebar />
          <div
            className={classnames(classes.content, {
              [classes.contentShift]: layoutState.isSidebarOpened,
            })}
          >
            <div className={classes.fakeToolbar} />
            <Switch>
              <Route path="/app/dashboard" component={Dashboard} />
              <Route path="/app/typography" component={Typography} />
              <Route path="/app/tables" component={Tables} />
              <Route path="/app/blogform" component={BlogForm} />
              <Route path="/app/trash" component={trashPage} />
              <Route exact path="/app/post" render={() => <Redirect to='/app/post/posts' />} />
              <Route path='/app/post/posts' component={AddPost} />
              <Route path='/app/post/postlist' component={ViewPostList} />
              <Route path='/app/post/postcat' component={Categories} />
              <Route path='/app/post/trashlist' component={TrashList} />

              <Route path="/app/notifications" component={Notifications} />
              <Route
                exact
                path="/app/ui"
                render={() => <Redirect to="/app/ui/icons" />}
              />
              <Route path="/app/ui/maps" component={Maps} />
              <Route path="/app/ui/icons" component={Icons} />
              <Route path="/app/ui/charts" component={Charts} />

              
              
            </Switch>
            <Box
              mt={5}
              width={"100%"}
              display={"flex"}
              alignItems={"center"}
              justifyContent="space-between"
            >
              <div>
                {/* <Link
                  color={'primary'}
                  href={'#'}
                  target={'_blank'}
                  className={classes.link}
                >
                  Flatlogic
                </Link> */}
                {/* <Link
                  color={'primary'}
                  href={'#'}
                  target={'_blank'}
                  className={classes.link}
                >
                  About Us
                </Link> */}
                {/* <Link
                  color={'primary'}
                  href={'#'}
                  target={'_blank'}
                  className={classes.link}
                >
                  Blog
                </Link> */}
              </div>
              <div>
                <Link
                  href={'#'}
                  target={'_blank'}
                >
                  <IconButton aria-label="facebook">
                    <Icon
                      path={FacebookIcon}
                      size={1}
                      color="#6E6E6E99"
                    />
                  </IconButton>
                </Link>
                <Link
                  href={'#'}
                  target={'_blank'}
                >
                  <IconButton aria-label="#">
                    <Icon
                      path={TwitterIcon}
                      size={1}
                      color="#6E6E6E99"
                    />
                  </IconButton>
                </Link>
                <Link
                  href={'#'}
                  target={'_blank'}
                >
                  <IconButton
                    aria-label="github"
                    style={{marginRight: -12}}
                  >
                    <Icon
                      path={GithubIcon}
                      size={1}
                      color="#6E6E6E99"
                    />
                  </IconButton>
                </Link>
              </div>
            </Box>
          </div>
        </>
    </div>
  );
}

export default withRouter(Layout);
