import React ,{ useEffect, useState } from 'react'
import { Grid, Icon ,Tooltip } from "@material-ui/core";
import { makeStyles } from "@material-ui/styles";
import MUIDataTable from "mui-datatables";
import { Button } from '@material-ui/core';
import 'react-toastify/dist/ReactToastify.css';
// import { DeleteIcon }  from "@material-ui/icons";
import DeleteIcon from '@material-ui/icons/Delete';
import EditIcon from '@material-ui/icons/Edit';
import VisibilityIcon from '@material-ui/icons/Visibility';


// components
import PageTitle from "../../components/PageTitle/PageTitle";
import Widget from "../../components/Widget/Widget";
import { DragIndicatorRounded, Message } from '@material-ui/icons';
import RestoreFromTrashIcon from '@material-ui/icons/RestoreFromTrash';
import DraggableDialog from '../blogform/deleteConfirm/DraggableDialog';



const BlogForm = ( ...props ) => {
  const [tableData, setTableData] = useState([])
  const [sentDataToggler, setSentDataToggler] = useState(false);
  const [showDelete, setShowDelete] = useState(false);
  const [userId, setUserId] = useState();
  const [reqtype, setReqType] = useState('');


  const forDataLoop = data => data.forEach(ele=> setTableData(pre => [ ...pre, Object.values(ele) ]))
  


  
  
   
        
          useEffect(() => {
         
            const fetchData = () => {
            fetch('https://test-fevison.herokuapp.com/contact/trash')
            .then(response => response.json())
            .then(data => {
                forDataLoop(data)
                console.log(data)
              })
            }
            fetchData();
            return (()=>setTableData(()=>[]))
          },[sentDataToggler])
          
          
          
          const onReStoreHandaler = (value) => {
            fetch('http://localhost:4000/contact/restore', {
                method: 'POST', // or 'PUT'
                headers: {
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify({_id: userId, status: "active"}),
                })
                .then(response => response.json(userId))
                .then(data => {
                    console.log('Success:', 'restore');
                })
                .catch((error) => {
                    console.error('Error:', error);
                });

                setSentDataToggler(()=>!sentDataToggler)

          }
        
    
        
        


  

    
      // getUserData.forEach(element => {
      //   datatableData.push(Object.values(element)) 
      // });  
      const onDeleteHandaler =() =>{
        fetch('http://localhost:4000/contact/delete', {
                method: 'POST', // or 'PUT'
                headers: {
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify({userId}),
                })
                .then(response => response.json(userId))
                .then(data => {
                    console.log('Success:', 'dlete');
                })
                .catch((error) => {
                    console.error('Error:', error);
                });
                console.log('requested to delete');

                setSentDataToggler(()=>!sentDataToggler)
      }
        const useStyles = makeStyles(theme => ({
          tableOverflow: {
            overflow: 'auto'
          }
        }))
        const confirmDelete = () =>{
          console.log(reqtype)
          if (reqtype == 'delete'){
            onDeleteHandaler()
          
          setReqType(()=>'')
          setUserId(()=>'')
         
        }}
        const tableRowView =(value,type) => {
          // console.log(`click ${value}`)
          const result =tableData.find(( [ _id ] ) => _id == value)
          console.log(result)
          setUserId(()=>result[1])
          setReqType(()=>type)
          if (type == 'restore'){
          onReStoreHandaler()
          setUserId(()=>'')

          }else{
          setShowDelete(()=>true)
          }
          
          // setModalType(()=>type)
          // setShowModal(()=>true)
          // setModalData(()=>result)
        }

        
          const classes = useStyles();
          const columnsForTable = [
            {
              name: "Actions",
              options: {
                customBodyRender: (value, tableMeta, updateValue) => {

                  return (
                    <>
                    <Tooltip title="Restore" arrow>
                        <Button onClick={ ()=>tableRowView(value,'restore')}>
                          <RestoreFromTrashIcon />
                        </Button>
                    </Tooltip>
                    <Tooltip title="Delete Parmanent" arrow>
                      <Button onClick={()=>tableRowView(value,'delete')}>
                        <DeleteIcon />
                      </Button>
                  </Tooltip>
                </>
                  );
                }
              }
            },
            "Name",
            "Messege", 
            "Email",
            "Subject"
          ];
         const sentDataToggalHandler = () =>{
          setSentDataToggler((toggal)=>!toggal)
         }
          
          return (
            <>
            
              <PageTitle title="Trash List" />
              <Grid container spacing={4}>
              {showDelete && <DraggableDialog setOpen={true} controlAction={confirmDelete} setOpenDilog={()=>setShowDelete(()=>false)} message={'Are You Sure Want To Delete ?'} />   
               }
                <Grid item xs={12}>
          <MUIDataTable
            title="Blog List"
            data={tableData}
            columns={columnsForTable}
            
            options={{
              filterType: "checkbox",
              textLabels: {
                body: {
                  noMatch: " Data Loading...",
                  toolTip: "Sort",
                  columnHeaderTooltip: column => `Sort for ${column.label}`
                },
            }}}
          />
        </Grid>

                
              </Grid>
              
            </>
          );
        }
        
    


export default BlogForm
