import React, { useState, useEffect, } from 'react'
import { makeStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import Button from '@material-ui/core/Button';
import PageTitle from "../../../components/PageTitle/PageTitle";
import { Dialog,Grid, Box } from '@material-ui/core';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import MuiAlert from '@material-ui/lab/Alert';
import TextField from '@material-ui/core/TextField';
import SnackbarAlert from '../alert/SnackbarAlert';




import DeleteForeverIcon from '@material-ui/icons/DeleteForever';
import DeleteIcon from '@material-ui/icons/Delete';
import EditIcon from '@material-ui/icons/Edit';
import { mdiConsolidate } from '@mdi/js';
import { FormControl } from '@material-ui/core';


const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
    flexWrap: 'wrap',
  },
  margin: {
    margin: theme.spacing(1),
  },
  withoutLabel: {
    marginTop: theme.spacing(3),
  },
  textField: {
    width: '25ch',
  },
  table: {
    minWidth: 650,
  },
}));


  function createData(name, calories, fat, carbs, protein) {
    return { name, calories, fat, carbs, protein };
  }
  const rows = [
    createData('Frozen yoghurt', `but also the leap into electronic typesetting, remaining essentially unchanged. It was popularised in the 1960s with the release of Letraset sheets containing Lorem Ipsum passages, and more recently with desktop publishing software like Aldus PageMaker including versions of Lorem Ipsum.
    Why do we use it?
    `
    , 6.0, 24, 4.0),
    createData('Ice cream sandwich', 237, 9.0, 37, 4.3),
    createData('Eclair', 262, 16.0, 24, 6.0),
    createData('Cupcake', 305, 3.7, 67, 4.3),
    createData('Gingerbread', 356, 16.0, 49, 3.9),
  ];

const Categories = () => {
    const classes = useStyles();
    const [categoryList, setCategoryList] = useState([])
    const [deleteDilogToggal,setDeleteDilogToggal] = useState(false)
    const [userId, setUserId] = useState()
    const [updateButtonToggale, setUpdateButtonToggale]= useState(false)
    const [formDetail, setFormDetail] = useState({title:null,description:null});
    const [urlRefresh, setUrlRefresh] = useState(false)
    const [alertConfig, setAlertConfig] = useState({open:false,type:'success',message:"Your Message Has Been Complited"})
    console.log(alertConfig)
    const addCategory = (value) => {

      fetch('https://test-fevison.herokuapp.com/category/add', {
        method: 'POST',
        body:JSON.stringify(value),
        headers: {
        'Content-Type': 'application/json',
       },
      }).then(respose=>respose.json())
      .then(doc=>{
        console.log(doc)
        setAlertConfig({open:true, type:'success', message:doc.message })
        setUrlRefresh((pre)=>!pre)
        disapearAlert();
      })
      .catch(err=>console.log(err))
    }


    const updateCategory = (value) => {

      fetch('https://test-fevison.herokuapp.com/category/upd', {
        method: 'PUT',
        body:JSON.stringify(value),
        headers: {
        'Content-Type': 'application/json',
       },
      }).then(respose=>respose.json())
      .then(doc=>{
        setAlertConfig({open:true, type:'success', message: doc.message })
        disapearAlert();
        setUrlRefresh((pre)=>!pre)
      })
      .catch(err=>console.log(err))
    }


    const deleteCategory = (value) => {

      fetch('https://test-fevison.herokuapp.com/category/del', {
        method: 'DELETE',
        body:JSON.stringify(value),
        headers: {
        'Content-Type': 'application/json',
       },
      }).then(respose=>respose.json())
      .then(doc=>{
        console.log(doc)
        setAlertConfig({open:true, type:'success', message:doc.message })
        disapearAlert();
        setUrlRefresh((pre)=>!pre)


      })
      .catch(err=>console.log(err))
      setDeleteDilogToggal(()=>false)
    }



    useEffect(() => {
      fetch('https://test-fevison.herokuapp.com/category/', {
        method: 'GET',
        headers: {
        'Content-Type': 'application/json',
       },
    

      }).then(respose=>respose.json())
      .then(doc=>{
        doc.map((ele)=>setCategoryList((pre)=>[...pre, ele]))
        console.log(categoryList)
      })
      .catch(err=>console.log(err))
      return ( () => setCategoryList(()=>[]))
      
    }, [urlRefresh])


    const editHandle=(value)=>{
      setFormDetail(()=>{
        return{
          userId:value._id,
          title:value.categoryTitle,
          description:value.description
        }
      })
      setUpdateButtonToggale(true)
      console.log(formDetail)
    }
    
    const deleteCategoryHandler = (value)=>{
      setDeleteDilogToggal(()=>true)
      setUserId(()=>value)  
    }
    const submitHandler = (event) => {
      console.log(event)
      event.preventDefault()
      console.log(formDetail)
      if(formDetail.title == '' || formDetail.description == ''){
        return  console.log("input field can be empty")
      }
      addCategory(formDetail)
      setFormDetail((pre) => {
        return { ...pre ,title:'', description:"", }})

    }

    const inputHandler = (key,value) => {
      setFormDetail(pre =>{
        return {
          ...pre,
          [key]:value
        }
      }
      )
    }
    const updateHandler = () => {
       if(formDetail.title == '' || formDetail.description == ""){
         return console.log('input feild Cant be empty')
       }
       updateCategory(formDetail)
       setFormDetail(()=>{
        return {userId:'',
        title:'',
        description:''
        }
       })
      setUpdateButtonToggale(()=>false)
       

    } 
    const disapearAlert =(value)=>{
      setTimeout(() => {
      setAlertConfig({open:false, type:"", message:"", ...value })
      }, 2000);
    }

    const customDiloge = (
      <Dialog open={true}  aria-labelledby="form-dialog-title">
       <DialogContent>
          <DialogTitle id="form-dialog-title">Delete Confirmation</DialogTitle>
          <DialogContentText id="alert-dialog-description">Do You Realy Want To Delete This Category</DialogContentText>
       </DialogContent>
    
       <DialogActions>
          <Button  color="primary" onClick={()=>setDeleteDilogToggal(()=>false)}>
                CANCEL
          </Button>
          <Button onClick={()=>deleteCategory({userId: userId})} color="primary" autoFocus>
                Agree
          </Button>
       </DialogActions>
    
    </Dialog>
    )

    return (
        <>
        {deleteDilogToggal && customDiloge}
        <PageTitle title="Blog Categories" />
        <SnackbarAlert open={alertConfig.open} type={alertConfig.type} message={alertConfig.message} />
        
        <div>


     <form className={classes.root} onSubmit={(event)=>submitHandler(event)} autoComplete="off" enctype="multipart/form-data">
     <Box width="1">
     <TextField
          id="outlined-multiline-flexible"
          label="Title"
          name="title"
          value={formDetail ? formDetail.title : ''}
          fullWidth
          multiline
          variant="outlined"
          onChange={(event)=>inputHandler(event.target.name,event.target.value)}
        />
     </Box>
       
     <Box py={1} width="1">
        <TextField
          id="outlined-multiline-static"
          label="Description"
          name="description"
          value={formDetail.description}
          multiline
          fullWidth
          rows={4}
          variant="outlined"
          onChange={(event)=>inputHandler(event.target.name,event.target.value)}
        />
        </Box>
        <Box py={2}>
         {updateButtonToggale ? <> <Button variant="contained" color="primary" onClick={updateHandler} children={"Update"} />
         <Button variant="contained" color="primary" children={'Add New'} onClick={()=>setUpdateButtonToggale(()=>false)} /></>
          : <Button variant="contained" color="primary" type="submit" children={'Submit'} /> 
             
          }
         
        </Box>


     </form>
     </div>



    <TableContainer component={Paper}>
      <Table className={classes.table} aria-label="simple table">
        <TableHead>
          <TableRow>
          <TableCell>Action</TableCell>
            <TableCell>Categories Title</TableCell>
            <TableCell align="right">Description</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {categoryList.map((row) => (
            <TableRow key={row._id}>
              <TableCell align="th">
                  <Button onClick={()=>editHandle(row)}>
                    <EditIcon />
                  </Button>
                  <Button onClick={()=>deleteCategoryHandler(row._id)}>
                    <DeleteForeverIcon />
                  </Button>
              </TableCell>

              <TableCell component="th" scope="row">
                {row.categoryTitle}
              </TableCell>
              <TableCell align="right">{row.description}</TableCell>
              
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </TableContainer>
    </>

  );
}
            



export default Categories
