import React, { useState } from 'react'

import { makeStyles } from '@material-ui/core/styles';
import FilledInput from '@material-ui/core/FilledInput';
import FormControl from '@material-ui/core/FormControl';
import FormHelperText from '@material-ui/core/FormHelperText';
import Input from '@material-ui/core/Input';
import InputLabel from '@material-ui/core/InputLabel';
import OutlinedInput from '@material-ui/core/OutlinedInput';
import { Close as CloseIcon } from "@material-ui/icons";

import { Grid } from "@material-ui/core";
import { TextareaAutosize } from '@material-ui/core';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import { Message } from '@material-ui/icons';
import classnames from "classnames";
import { useHistory } from 'react-router-dom';
import Notification from "../../../components/Notification/Notification";
import DraggableDialogcustom from '../deleteConfirm/DraggableDialog';
// import ImageForm from './imageForm/ImageForm';
// import DropImage from './dropImage/DropImage'
import { DropzoneArea } from 'material-ui-dropzone';






// import needed components, functions and styles
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';


const positions = [
    toast.POSITION.TOP_LEFT,
    toast.POSITION.TOP_CENTER,
    toast.POSITION.TOP_RIGHT,
    toast.POSITION.BOTTOM_LEFT,
    toast.POSITION.BOTTOM_CENTER,
    toast.POSITION.BOTTOM_RIGHT,
  ];

const useStyles = makeStyles((theme) => ({
    root: {
        '& > *': {
            margin: theme.spacing(1),
        },
    },
}));

export default function ComposedTextField({ ...props }) {
    const [name, setName] = useState(props.formData[1]);
    const [email, setEmail] = useState(props.formData[3]);
    const [subject, setSubject] = useState(props.formData[4]);
    const [message, setMessege] = useState(props.formData[2]);
    const [errorToastId, setErrorToastId] = useState(null);
    const [image, setImage] = useState([]);
    const [notificationsPosition, setNotificationPosition] = useState(2);
    const [confirmPageToggler, setConfirmPageToggler] = useState(true);
    const [fileObjects, setFileObjects] = useState([]);
    // const [formData, setFormData] = useState({});
    



    const history = useHistory();


   const sendRequest = async (formData,req) => {
    switch(req) {
    
        case 'edit':
            fetch('http://localhost:4000/contact/update', {
            method: 'POST', // or 'PUT'
            headers: {
                'Content-Type': 'application/json',
            },
            body: JSON.stringify(formData),
            })
            .then(response => response.json())
            .then(data => {
                console.log('Success:', formData);
            })
            .catch((error) => {
                console.error('Error:', error);
            });
            console.log('requested fo update')
          // code block
          break;
        case 'delete':

            fetch('http://localhost:4000/contact/delete', {
            method: 'POST', // or 'PUT'
            headers: {
                'Content-Type': 'application/json',
            },
            body: JSON.stringify(formData),
            })
            .then(response => response.json())
            .then(data => {
                console.log('Success:', formData);
            })
            .catch((error) => {
                console.error('Error:', error);
            });
            console.log('requested to delete')
            break;
        default:
            fetch('http://localhost:4000/contact/send', {
            method: 'POST',
            body: formData,
            })
            .then(response => response.json())
            .then(data => {
                console.log('Success:', formData.values());
            })
            .catch((error) => {
                console.error('Error:', error);
            });
            console.log("requested for send")
        
      }   
        
    }

    const retryErrorNotification = () => {
        var componentProps = {
          type: "message",
          message: "Message was sent successfully!",
          variant: "contained",
          color: "success",
        };
        toast.update(errorToastId, {
          render: <Notification {...componentProps} />,
          type: "success",
        });
        setErrorToastId(null);
      }

    const CloseButton = ({ closeToast, className }) => {
        return <CloseIcon className={className} onClick={closeToast} />;
    }
    const sendNotification = (componentProps, options) => {
        return toast(
          <Notification
            {...componentProps}
            className={classes.notificationComponent}
          />,
          options,
        );
      }
    const  handleNotificationCall = (notificationType) =>{
        var componentProps;
    
        if (errorToastId && notificationType === "error") return;
    
        switch (notificationType) {
          case "info":
            componentProps = {
              type: "feedback",
              message: "New user feedback received",
              variant: "contained",
              color: "primary",
            };
            break;
          case "error":
            componentProps = {
              type: "message",
              message: "Message was not sent!",
              variant: "contained",
              color: "secondary",
              extraButton: "Resend",
              extraButtonClick: retryErrorNotification,
            };
            break;
          default:
            componentProps = {
              type: "shipped",
              message: "The item was shipped",
              variant: "contained",
              color: "success",
            };
        }
        
        var toastId = sendNotification(componentProps, {
          type: notificationType,
          position: positions[notificationsPosition],
          progressClassName: classes.progress,
          onClose: notificationType === "error" && (() => setErrorToastId(null)),
          className: classes.notification,
        });
    
        if (notificationType === "error") setErrorToastId(toastId);
      }
    
      const changeNotificationPosition = (positionId) => {
        setNotificationPosition(positionId);
      }
    
    
    // #############################################################
    // const CloseButton = ({ closeToast, className }) => {
    //   return <CloseIcon className={className} onClick={closeToast} />;
    // }


    const classes = useStyles();

    const handleChangeName = (event) => {
        setName(event.target.value);
    };
    const handleChangeEmail = (event) => {
        setEmail(event.target.value);
    };
    const handleChangeSubject = (event) => {
        setSubject(event.target.value);
    };
    const handleChangeMessege = (event) => {
        setMessege(event.target.value);
    };

    const HandlerSubmit = () => {
        if ((name == '') || (email == '') || (message == '') || (subject == '' )) {
            console.log(`input field can't be empty`);
        } else {

            setMessege('');
            setName('');
            setEmail('');
            setSubject('');
            

            let formData = new FormData();
            
            formData.append('name',name);
            // console.log(fDta)
            formData.append('email',email);
            formData.append('message',message);
            formData.append('subject',subject);
            image.forEach(element => {
              formData.append('image',element)
            });
            // formData.append('image',image[0]});
            
            // setFormData(()=>formInfo)
            for (var value of formData.values()) {
              console.log(value);
           }
          //  image.map(element => {
          //  return console.log(element.p)
          
          //  });
          console.log(image[0])

            

            // const formData = {
            //     _id:props.formData[0],
            //     name: name,
            //     message: message,
            //     email: email,
            //     subject: subject,
            //     image: image

            // }

            // formData.append('image',image[0].name)
            // let formData =new FormData;
            
            sendRequest(formData,(props.reqType))
            props.sentDataToggal(true)
        
            // props.handleNotificationCall('success')
        }

    }
    // console.log(image[0].name)
      
    
    


    return (
        <>

           <ToastContainer
          className={classes.toastsContainer}
          closeButton={
            <CloseButton className={classes.notificationCloseButton} />
          }
          closeOnClick={false}
          progressClassName={classes.notificationProgress}
        />
          <DraggableDialogcustom setOpen={false} />
            {/* <ToastContainer className={classes.toastsContainer} closeButton={<CloseButton className={classes.notificationCloseButton} />} closeOnClick={false} progressClassName={classes.notificationProgress} /> */}

            <Grid xs={12}>
                <form className={classes.root} noValidate autoComplete="off" enctype="multipart/form-data">


                    <FormControl variant="outlined" fullWidth>


                        <TextField
                            id="outlined-multiline-static"
                            label="Message"
                            multiline
                            rows={10}
                            value={message}
                            variant="outlined"
                            onChange={handleChangeMessege}
                            placeholder={props.formData[2]}
                        />
                    </FormControl>

                    <Grid container justifyContent="space-between" direction="row">
                        <Grid item xs={6}>
                            <FormControl variant="outlined" fullWidth>
                                <InputLabel htmlFor="component-outlined">Name</InputLabel>
                                <OutlinedInput id="component-outlined" placeholder={props.formData[1]} value={name} onChange={handleChangeName} label="Name" />
                            </FormControl>
                        </Grid>
                        <Grid item xs={6}>
                            <FormControl variant="outlined" fullWidth>
                                <InputLabel htmlFor="component-outlined">Email</InputLabel>
                                <OutlinedInput id="component-outlined" value={email} onChange={handleChangeEmail} placeholder={props.formData[3]} label="Name" />
                            </FormControl>
                        </Grid>
                    </Grid>
                    <FormControl variant="outlined" fullWidth>
                        <InputLabel htmlFor="component-outlined">Subject</InputLabel>
                        <OutlinedInput id="component-outlined" value={subject} onChange={handleChangeSubject} label="Name" placeholder={props.formData[4]} />
                    </FormControl>
                    
                    {/* <ImageForm /> */}
                    {/* <DropImage /> */}
                    <DropzoneArea filesLimit={999}  onAdd={newFileObjs => {
                       console.log('onAdd', newFileObjs);
                       setFileObjects([].concat(fileObjects, newFileObjs));
                     }} onChange={(files) => setImage(()=>(files)) } clearOnUnmoun={true}  filesLimit={3} />
                    
      
                </form>

            </Grid>
            <Grid xs={8}>
                <Button variant="contained" color="secondary" onClick={HandlerSubmit}>
                    Submit
                </Button>
            {/* <Button variant="contained" colortheme="success" onClick={() => props.handleNotificationCall('success')} className={classnames(classes.notificationCallButton)}>Success Message</Button> */}
            {/* <Button variant="contained"  onClick={() => props.handleNotificationCall('success')} >Default</Button> */}
            <Button
                variant="contained"
                color="primary"
                onClick={() => handleNotificationCall("success")}
                className={classnames(classes.notificationCallButton)}
              >Submit</Button>
            </Grid>
        </>
    );





}
