import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Modal from '@material-ui/core/Modal';
import Typography from '@material-ui/core/Typography';
import Form from '../form/Form'
import { Button } from '@material-ui/core';
import DraggableDialog from '../deleteConfirm/DraggableDialog';

function rand() {
  return Math.round(Math.random() * 20) - 10;
}

function getModalStyle() {
  const top = 50 + rand();
  const left = 50 + rand();

  return {
    top: `${top}%`,
    left: `${left}%`,
    transform: `translate(-${top}%, -${left}%)`,
  };
}

const useStyles = makeStyles((theme) => ({
  paper: {
    position: 'absolute',
    width: 800,
    backgroundColor: theme.palette.background.paper,
    border: '2px solid #000',
    boxShadow: theme.shadows[5],
    padding: theme.spacing(2, 4, 3),
  },
}));

export default function SimpleModal(props) {
  const classes = useStyles();
  // getModalStyle is not a pure function, we roll the style only on the first render
  const [modalStyle] = React.useState(getModalStyle);
  const [open, setOpen] = React.useState(true);
  const [delConTog, setDelConTog] = React.useState(false);


  const handleOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
    props.taggalModal()
  };
  const delConTogHand = () =>{
    setDelConTog(true)
  }
  const onDeleteHandaler =() =>{
    fetch('https://test-fevison.herokuapp.com/contact/delete', {
            method: 'POST', // or 'PUT'
            headers: {
                'Content-Type': 'application/json',
            },
            body: JSON.stringify({_id:props.modalData[0]}),
            })
            .then(response => response.json(props.modalData))
            .then(data => {
                console.log('Success:', 'dlete');
            })
            .catch((error) => {
                console.error('Error:', error);
            });
            console.log('requested to delete');
            props.taggalModal()
  }
  const onTrashHandaler =() =>{
    fetch('https://test-fevison.herokuapp.com/contact/movetrash', {
            method: 'POST', // or 'PUT'
            headers: {
                'Content-Type': 'application/json',
            },
            body: JSON.stringify({_id:props.modalData[1], status:'deActive'}),
            })
            .then(response => response.json(props.modalData))
            .then(data => {
                console.log('Success:', 'tresed');
            })
            .catch((error) => {
                console.error('Error:', error);
            });
            props.taggalModal()
  }
  const viewBody = (
    <div style={modalStyle} className={classes.paper}>
      <Typography elementType={'h1'} variant={'h1'}>Name: {props.modalData[1]}</Typography>
      <Typography elementType={'p'} variant={'h6'}>Email: {props.modalData[3]}</Typography>

      <Typography paragraph={'h6'} elementType={'p'} variant={'h4'}>Message: {props.modalData[2]}</Typography>
      <Typography elementType={'p'} variant={'h5'}>subject: {props.modalData[4]}</Typography>
  
    </div>
  );
  const editBody = ( 
    <>
    <div style={modalStyle} className={classes.paper}>
          <Form formData={props.modalData} reqType={'edit'} sentDataToggal={props.sentDataToggal} />        
    </div>
    </>

  );
  const deleteBody = (
    <>
    {delConTog && <DraggableDialog setOpen={true} setOpenDilog={()=>setDelConTog(()=>false)} controlAction={onDeleteHandaler} userData={[props.modalData]} />}
    <div style={modalStyle} className={classes.paper}>
      <Typography elementType={'h1'} variant={'h1'}>Name: {props.modalData[1]}</Typography>
      <Typography elementType={'p'} variant={'h6'}>Email: {props.modalData[3]}</Typography>

      <Typography paragraph={'h6'} elementType={'p'} variant={'h4'}>Message: {props.modalData[2]}</Typography>
      <Typography elementType={'p'} variant={'h5'}>subject: {props.modalData[4]}</Typography>
      <Button variant="contained" color="secondary" onClick={onTrashHandaler}>Move to Trash</Button>
      <Button color="secondary" onClick={delConTogHand}>Delete forever</Button>

  
    </div>
    </>

  );
  const bodyRender = () =>{
    if (props.modalType == 'view') { return viewBody}
    if (props.modalType == 'edit') { return editBody}
    if (props.modalType == 'delete') { return deleteBody}
  }

  return (
    <div>
      
      <button type="button" onClick={handleOpen}>
        Open Modal
      </button>
      <Modal
        open={open}
        onClose={handleClose}
        aria-labelledby="simple-modal-title"
        aria-describedby="simple-modal-description"
      >
       {bodyRender()}
      </Modal>
    </div>
  );
}
